import React, { Component } from 'react';
import {NavLink} from 'react-router-dom'; 

class Nav extends Component {
  render() {

    return (
        <nav>
            <ul>
              <li><NavLink exact activeClassName={"active"} to='/newhome2018'>Home</NavLink></li>
              <li><NavLink exact activeClassName={"active"} to='/dashboard'>Dashboard</NavLink></li>
              <li><NavLink exact activeClassName={"active"} to='/test'>Test</NavLink></li>
            </ul>
        </nav>
    );
  }
}

export default Nav;



